
@mixin menu-positioning() {
	position: fixed;
	display: block;
}
@mixin left-siders() {
	float: left;
}
$main-color: #00c4cc;
$menu-color: #272C33;

body{
	background-color: #EBECED;
}
.logo{
	font-size: 20px;
	@include left-siders;
}
.drop-down{
	float:right;
}
.navbar{
	@include menu-positioning;
	color: #fff;
	background-color: $main-color;
	width: 100%;
	padding: 10px;
	z-index: 100;
	height:50px;
}
.menu{
	background-color: $menu-color;
	@include left-siders;
	@include menu-positioning;
	height: 100%;
	width: 90px;
	margin-top: 50px !important;
	ul{
		list-style: none;
		margin-left: 0px;
	}
}
.menu-container{
	position: relative;
	text-align: center;
	padding: 5px;
	margin-top: 10px;
	margin-bottom: 10px;
}
.men-li, .menu-ico, .menu-lab{
	text-transform: uppercase;
	color: rgba(255,255,255, .5);
}
.menu-ico{
	font-size: 35px !important;
	text-align: center;
}
.menu-lab{
	font-size: 10px !important;
}
.wizard-component{
		width:30%;
		background-color: #fff;
		border-right: 1px solid $main-color;
		@include left-siders;
		margin-left: 90px;
		margin-top: 50px;
		height: 100%;
		display: block;
		position:absolute;
		overflow-y: scroll;
		-webkit-box-shadow: 2px 0px 8px 0px rgba(50, 50, 50, 0.25);
  		-moz-box-shadow:    2px 0px 8px 0px rgba(50, 50, 50, 0.25);
  		box-shadow:         2px 0px 8px 0px rgba(50, 50, 50, 0.25);
}
.sbi-stage__stages-container {
    margin-bottom: 15px;
    padding: 20px 0;
    display: block;
    vertical-align: top;
    text-rendering: optimizelegibility;
    text-align: center;
    &:before{
    	content: " ";
    	display: table;
    }
    &:after{
    	content: " ";
	    display: table;
	    clear: both;
    }
}
.sbi-stage, .sbi-stage__container {
    display: inline-block;
    position: relative;
}
.sbi-stage_active {
    margin: 0 2px;
}
.sbi-stage {
    vertical-align: middle;
    cursor: pointer;
}
.sbi-stage_0 .sbi-stage__icon {
    background-image: url(data:image/png;base64,);
}
.sbi-stage_1 .sbi-stage__icon {
    background-image: url(data:image/png;base64,);
}
.sbi-stage_2 .sbi-stage__icon {
    background-image: url(data:image/png;base64,);
}
.sbi-stage_3 .sbi-stage__icon {
    background-image: url(data:image/png;base64,);
}
.sbi-stage_active .sbi-stage__icon {
    height: 60px;
    width: 60px;
    opacity: 1;
}
.sbi-stage__icon {
    position: relative;
    height: 40px;
    width: 40px;
    background-repeat: no-repeat;
    background-size: contain;
    opacity: .6;
}
.sbi-stage_active .sbi-stage__title {
    font-size: 12px;
    color: #898da5;
    font-weight: 500;
    bottom: -26px;
}
.sbi-stage__title {
    position: absolute;
    left: 50%;
    bottom: -24px;
    font-size: 9px;
    color: #d2d4dd;
    width: 90px;
    margin-left: -45px;
    text-align: center;
    font-weight: 500;
}
.sbi-stage_completed .sbi-stage__icon:before {
    background: #00e9a4;
}
.sbi-stage_active .sbi-stage__icon:before {
    top: 3px;
    right: 6px;
    width: 12px;
    height: 12px;
}
.sbi-stage__icon:before {
    content: "";
    position: absolute;
    top: 1px;
    right: 2px;
    width: 10px;
    height: 10px;
    z-index: 10;
    border: 2px solid #7e839d;
    border-radius: 30px;
}
.sbi-stage__dot {
    display: inline-block;
    vertical-align: middle;
    background: #dadada;
    border-radius: 100px;
    width: 4px;
    height: 4px;
    margin: 0 4px;
}
.progress-bar {
    position: relative;
    margin: 0px;
}
.progress-bar__title {
    font-size: 9px;
    text-transform: uppercase;
    font-weight: 500;
    color: #7e839d;
    padding-bottom: 10px;
}
.progress-bar__track {
    height: 6px;
    border-radius: 3px;
    background: #d8d8d8;
}
.progress-bar__fill {
    position: absolute;
    left: 50%;
    height: 6px;
    background: linear-gradient(90deg,#01f1a4,#24bcac);
    border-radius: 3px;
}
.progress-bar__label {
    position: absolute;
    background: #383846;
    color: #fff;
    top: 40px;
    margin-left: -19px;
    font-size: 9px;
    border-radius: 10px;
    padding: 4px 0;
    font-weight: 500;
    line-height: 1;
    width: 36px;
    text-align: center;
}
.progress-bar__label:before {
    content: "";
    position: absolute;
    margin-left: -10px;
    border-radius: 10px;
    background-color: #383846;
    top: -3px;
    margin-left: -3px;
    height: 6px;
    width: 6px;
    left: 50%;
}
.progress-bar__sbi-stage_container .info-tip__container {
    position: absolute;
    top: 0px;
    right: 20px;
}
.wizard-que{
  text-align: center;
  color: #4c575f;
  font-size: 12px;
  font-weight: 500;
& .wiz-text{
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  color: #7e839d;
  border: 1px solid #a8b6bd;
  border-radius: 3px;
  white-space: nowrap;
  margin-top: 3px;
  font-size: 12px;
}
& .wiz-textarea{
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  color: #7e839d;
  border: 1px solid #a8b6bd;
  border-radius: 3px;
  white-space: nowrap;
  margin-top: 3px;
  font-size: 12px;
  height: 100px;
  padding: 0.5rem;
}
}
.wiz-more{
  color: #00C4CC !important;
}
.wizard-tab{
	width: (100%/3);
	text-align: center;
}
.widget-container{
	margin-top: 15px;
}
/*WIDGET EFFECT*/
.grid {
	max-width: 69em;
	list-style: none;
	margin: 30px auto;
	padding: 0;
	li {
	display: block;
	float: left;
	padding: 7px;
	width: 33%;
	opacity: 0;
	}
	li.shown,.no-js .grid li,.no-cssanimations .grid li {
	opacity: 1;
	}

	& li a,& li img {
	outline: none;
	border: none;
	display: block;
	max-width: 100%;
	}
}
/* Effect 6: flip (based on http://lab.hakim.se/scroll-effects/ by @hakimel) */
.grid.effect-6 {
	-webkit-perspective: 1300px;
	perspective: 1300px;
	& li.animate {
	-webkit-transform-style: preserve-3d;
	transform-style: preserve-3d;
	-webkit-transform-origin: 0% 0%;
	transform-origin: 0% 0%;
	-webkit-transform: rotateX(-80deg);
	transform: rotateX(-80deg);
	-webkit-animation: flip .8s ease-in-out forwards;
	animation: flip .8s ease-in-out forwards;
	}
}
@-webkit-keyframes flip {
	0% { }
	100% { -webkit-transform: rotateX(0deg); opacity: 1; }
}

@keyframes flip {
	0% { }
	100% { -webkit-transform: rotateX(0deg); transform: rotateX(0deg); opacity: 1; }
}

@media screen and (max-width: 900px) {
	.grid li {
		width: 50%;
	}
}

@media screen and (max-width: 400px) {
	.grid li {
		width: 100%;
	}
}
